import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

/*eslint-disable*/
import React from "react";
import classNames from "classnames";
import PropTypes from "prop-types";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Tooltip from "@material-ui/core/Tooltip";

// core components
import CustomDropdown from "@components/CustomDropdown/CustomDropdown.js"
import Button from "@components/customButtons/button.js"

import styles from "assets/jss/components/layout/header/linkStyle.js"

import { useStaticQuery, graphql, Link } from "gatsby"

const useStyles = makeStyles(styles);

PageHeaderHeaderLinks.propTypes = {
  pageNode: PropTypes.object.isRequired,
}

export default function PageHeaderHeaderLinks({ pageNode }) {
  const data = useStaticQuery(
    graphql`
      query {
        mainMenus: allThreeMysticApesPage(sort: {fields: page___pageKey, order: ASC}, filter: {page: {includeMenu: {in: "mainMenu"}}}) {
          edges {
            node {
              page {
                title
                slug
              }
            }
          }
        }
        site {
          siteMetadata {
            linkedin
            twitter
          }
        }
      }      
    `
  )

  const pageSlug = (pageNode === null ? "" : (pageNode.context != null ? pageNode.context.slug : ""));

  const classes = useStyles();
  return (
    <nav>
      <List className={classes.list}>
        {data.mainMenus.edges.map(({ node }, index) => (
          <ListItem className={classes.listItem} key={index}>
            <Link
              to={`/${node.page.slug}`}
              className={pageSlug === `/${node.page.slug}` ? classNames(classes.navLink, classes.navLinkActive) : classes.navLink}
              activeClassName={classes.navLinkActive}
              role="menuitem"
              title={node.page.title}
              aria-label={node.page.ariaLabel}
            >
              {node.page.title}
            </Link>
          </ListItem>
        ))}


        <ListItem className={classes.listItem}>
          <Tooltip
            id="social-linkedin"
            title="linkedin"
            aria-label="linkedin"
            placement={(typeof window !== 'undefined' && window.innerWidth > 959) ? "top" : "left"}
            classes={{ tooltip: classes.tooltip }}
          >
            <Button
              href={`https://www.linkedin.com/in/${data.site.siteMetadata.linkedin}/`}
              target="_blank"
              color="transparent"
              className={classes.navLink}

            >
              <FontAwesomeIcon icon={['fab', 'linkedin']} aria-hidden="true" size="2x" />
            </Button>
          </Tooltip>
        </ListItem>
        <ListItem className={classes.listItem}>
          <Tooltip
            id="social-twitter"
            title="twitter"
            aria-label="twitter"
            placement={(typeof window !== 'undefined' && window.innerWidth > 959) ? "top" : "left"}
            classes={{ tooltip: classes.tooltip }}
          >
            <Button
              href={`https://www.twitter.com/${data.site.siteMetadata.twitter}/`}
              target="_blank"
              color="transparent"
              className={classes.navLink}

            >
              <FontAwesomeIcon icon={['fab', 'twitter']} aria-hidden="true" size="2x" />
            </Button>
          </Tooltip>
        </ListItem>
      </List>
    </nav>
  );
}
