import { backgroundColor } from "assets/jss/common/threemysticapes.js"

export const globalPageStyle = {
    "htmlRoot": {
        height: "100%",
        "&.dark": {
            "background-color": backgroundColor.dark.color
        }

    },
    "page": {
        height: "100%",
        "&.dark": {
            "background-color": backgroundColor.dark.color,
            color: backgroundColor.dark.textColor
        },
        "& > div:first-of-type": {
            height: "100%",
            "& > div:first-of-type": {
                height: "100%",
            }
        }
    },
    "parallaxPage": {
        height: "30vh",
        maxHeight: "300px",
    },
    "sectionBody": {
    },
    mainContent: {
        minHeight: "65vh",
        padding: "30px 0",
        "&.dark":{
            color: backgroundColor.dark.textColor
        }
    }

};

export default {
    globalPageStyle,
}